* {
  box-sizing: border-box;
}

html, body {
  background-color: #4f5d73;
  margin-bottom: 1.5rem;
}

button {
  color: #e0e0d1;
  cursor: pointer;
  background-color: #404b5c;
  border: 1px solid #303946;
  border-radius: 1rem;
  transition: background-color .2s, transform .2s, box-shadow .2s;
  box-shadow: 0 0 4px #ffffffc7;
}

button:hover {
  background-color: #303946;
  transform: translateY(-2px);
}

.header-container {
  text-align: center;
  padding: 1rem;
}

.header-title-wrapper {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.header-title {
  color: #e0e0d1;
  text-shadow: 2px 2px 4px #1d1d1db3;
  margin: 0;
  font-size: 3.2rem;
}

.header-logo {
  width: 6.2rem;
  height: auto;
}

.header-divider {
  color: #e0e0d1;
  width: 50%;
  margin: 2rem auto .1rem;
  box-shadow: 2px 2px 4px #00000027;
}

.header-user-info {
  color: #e0e0d1;
  text-shadow: 2px 2px 4px #1d1d1db3;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: .4rem;
  margin-bottom: .2rem;
  padding: 0;
  font-size: 1.4rem;
  font-style: italic;
  display: flex;
}

.header-btn {
  color: #282e38;
  background-color: #e0e0d1;
  border: 1px solid #c2c2b6;
  margin: 0;
  padding: .5rem;
  font-weight: bold;
  box-shadow: 2px 2px 4px #0000001a;
}

.header-btn:hover {
  background-color: #b3b3a8;
  box-shadow: 4px 6px 12px #0003;
}

.modal-backdrop {
  z-index: 1000;
  background-color: #0009;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-content {
  text-align: left;
  background: #e0e0d1;
  border-radius: 1rem;
  flex-direction: column;
  gap: 1rem;
  width: 90%;
  max-width: 400px;
  padding: 1rem 2rem;
  display: flex;
  position: relative;
}

.modal-content form {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.modal-btn {
  color: #fff;
  width: fit-content;
  padding: .5rem 1rem;
  box-shadow: 2px 2px 4px #0000001a;
}

.modal-content form h2 {
  margin-bottom: 0;
}

.modal-content label {
  flex-direction: column;
  gap: .25rem;
  font-weight: bold;
  display: flex;
}

.modal-content input, .modal-content select {
  border: 1px solid #aaa;
  border-radius: .25rem;
  padding: .4rem;
}

.modal-list {
  flex-direction: column;
  gap: .4rem;
  margin: 0;
  padding: 0 0 1rem;
  list-style: none;
  display: flex;
}

.modal-list-item {
  background-color: #fff;
  border: 1px solid #aaa;
  border-radius: .4rem;
  padding: .5rem .75rem;
  transition: background-color .2s;
}

.modal-list-hover:hover {
  color: #fff;
  cursor: pointer;
  background-color: #404b5c;
  border: 1px solid #303946;
}

.modal-list-title {
  margin-bottom: 0;
}

.modal-list-item a {
  color: #000;
  text-decoration: none;
  display: block;
}

.modal-list-hover:hover a {
  color: #fff;
}

.modal-delete {
  justify-content: flex-start;
  gap: .5rem;
  margin-top: 1rem;
  display: flex;
}

.modal-divider {
  margin-top: 1.2rem;
  margin-bottom: 1rem;
}

.modal-btn-container {
  justify-content: space-between;
  gap: .5rem;
  display: flex;
}

.modal-password-feedback {
  align-items: center;
  gap: .3rem;
  margin: 0;
  font-size: 1rem;
  line-height: .8;
  display: flex;
}

.modal-password-feedback.valid {
  color: green;
}

.modal-password-feedback.invalid {
  color: red;
}

.modal-close-btn {
  color: #000;
  box-shadow: none;
  background-color: #e0e0d1;
  border: none;
  font-size: 1.5rem;
  position: absolute;
  top: .2rem;
  right: .4rem;
}

.modal-close-btn:hover {
  background-color: #e0e0d1;
}

.error-modal-content {
  padding: 1rem;
}

.error-modal-title {
  color: #c00;
  margin: 0 0 .5rem;
  font-size: 1.5rem;
}

.error-modal-message {
  color: #300;
  margin: 0 0 1rem;
  font-size: 1rem;
}

.error-modal-close-btn {
  color: #fff;
  cursor: pointer;
  background-color: #c00;
  border: none;
  border-radius: 4px;
  padding: .5rem 1rem;
}

.error-modal-close-btn:hover {
  background-color: #900;
}

.start-main {
  text-align: center;
  margin-top: .5rem;
}

.start-btn-group {
  justify-content: center;
  gap: .5rem;
  margin-top: .7rem;
  display: flex;
}

.start-btn {
  padding: .5rem 1.1rem;
  font-size: 1rem;
}

.start-info {
  color: #e0e0d1;
  text-shadow: 1px 1px 2px #1d1d1d7e;
  font-size: 1.2rem;
  line-height: 1.4;
}

.start-info-container {
  background-color: #404b5c;
  border: 1px solid #323a47;
  border-radius: 1.5rem;
  width: 100%;
  max-width: 900px;
  margin: 2rem auto;
  padding: 1.5rem 2rem;
  box-shadow: 0 0 4px #ffffffc7;
}

.start-info-container h2 {
  margin-top: 0;
  font-size: 2rem;
}

.start-info p {
  margin: .2rem 0;
}

.start-info-list {
  list-style-type: none;
}

.start-example-img {
  border: 1px solid #323a47;
  border-radius: 1.5rem;
  max-width: 100%;
  margin-top: .5rem;
  box-shadow: 2px 2px 4px #00000027;
}

.home-main {
  flex-direction: column;
  gap: 1rem;
  max-width: 300px;
  margin: 0 auto 2rem;
  display: flex;
}

.home-btn {
  padding: .6rem 1rem;
  font-size: 1rem;
}

.home-input {
  width: 100%;
  margin-bottom: 1rem;
  padding: .4rem;
  font-size: 1rem;
  display: block;
}

.home-stats {
  color: #e0e0d1;
  text-shadow: 1px 1px 2px #1d1d1d7e;
  background-color: #404b5c;
  border: 1px solid #323a47;
  border-radius: 1.5rem;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 900px;
  margin: 0 auto .5rem;
  padding: 1.5rem 2rem;
  font-size: 1.2rem;
  display: flex;
  box-shadow: 0 0 4px #ffffffc7;
}

.home-stats h3 {
  margin-top: .2rem;
  margin-bottom: 1rem;
}

.home-stats p {
  margin: .4rem 0;
  line-height: 1;
}

.scrumboard-main {
  text-align: center;
  flex-direction: column;
  align-items: center;
  padding: .2rem 1.25rem;
  display: flex;
}

.scrumboard-title {
  color: #e0e0d1;
  text-shadow: 2px 2px 4px #1d1d1db3;
  margin-top: 0;
  margin-bottom: 1.25rem;
  font-size: 2.5rem;
}

.scrumboard-nav {
  flex-wrap: wrap;
  justify-content: center;
  gap: .625rem;
  margin-bottom: 2rem;
  display: flex;
}

.scrumboard-nav button {
  padding: .6rem 1rem;
  font-size: 1rem;
}

.scrumboard-task-controls {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: .5rem;
  margin-bottom: 1rem;
  display: flex;
}

.scrumboard-task-filter, .scrumboard-task-sort {
  cursor: pointer;
  color: #e0e0d1;
  background-color: #404b5c;
  border: 1px solid #303946;
  border-radius: .8rem;
  flex: 47%;
  width: 45%;
  min-width: 250px;
  margin-bottom: .5rem;
  padding: .4rem;
  font-size: 1rem;
  display: block;
  box-shadow: 0 0 4px #ffffffc7;
}

.scrumboard-task-columns {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.25rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.scrumboard-task-column {
  box-sizing: border-box;
  border-radius: .7rem;
  flex-shrink: 0;
  width: 21.875rem;
  padding: 1rem;
  box-shadow: 2px 4px 8px #0000001a;
}

.scrumboard-task-column.column-new {
  background-color: #c75c5c;
}

.scrumboard-task-column.column-in-progress {
  background-color: #f5cf87;
}

.scrumboard-task-column.column-finished {
  background-color: #76c2af;
}

.scrumboard-column-header {
  flex-direction: column;
  align-items: center;
  margin-bottom: .3rem;
  font-size: 1rem;
  display: flex;
}

.scrumboard-column-header h3 {
  margin: .3rem 0;
  font-size: 2rem;
}

.scrumboard-task-list {
  border-radius: .25rem;
  min-height: 9.375rem;
  padding: .625rem;
}

.scrumboard-task-list-empty {
  color: #353535;
  text-align: center;
  margin-top: 1rem;
  font-style: italic;
  font-weight: bold;
}

.scrumboard-task-card {
  color: #292929;
  text-align: left;
  background-color: #ffffffb4;
  border-radius: 12px;
  margin-bottom: .5rem;
  padding: .6rem .75rem .75rem;
  box-shadow: 2px 4px 8px #0000001a;
}

.scrumboard-task-card h4 {
  margin-top: 0;
  margin-bottom: .25rem;
  font-size: 1.3rem;
}

.scrumboard-task-card p {
  margin: .15rem 0;
  font-size: 1rem;
}

.scrumboard-task-card button {
  color: #fff;
  margin-top: .2rem;
  padding: .5rem .6rem;
}

.notfound-container {
  color: #e0e0d1;
  text-shadow: 1px 1px 2px #1d1d1d7e;
  text-align: center;
  background-color: #404b5c;
  border: 1px solid #323a47;
  border-radius: 1.5rem;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 700px;
  margin: 2rem auto;
  padding: 1.5rem 2rem;
  font-size: 1.2rem;
  display: flex;
  box-shadow: 0 0 4px #ffffffc7;
}

.notfound-title {
  color: #e0e0d1;
  margin-bottom: 1rem;
